<template lang="pug">
  .h5-editor.clear
    .opreate-block.fl
      button(@click="sendLogger()") send logger
      button(@click="addtest") test
      button(@click="undo", :disabled="!isVuexCaptureHaveUndo") 撤销
      button(@click="redo", :disabled="!isVuexCaptureHaveRedo") 重做
    div(style="border: 1px solid red;width: 100%;height: 100px") {{projectIndex}}
</template>
<script>
import index from './index'

export default {
  ...index
}
</script>
<style lang="stylus" scoped>
.h5-editor
  position relative
  padding-top 50px
  width 100%
  height 100%
  box-sizing border-box
  border 1px solid blue
  .opreate-block
    position absolute
    top 0
    left 0
    height 50px
    width 100%
    box-shadow 1px 1px 2px #ccc
  .statistics
    position absolute
    top 5px
    right 5px
</style>



