import { mapState, mapGetters } from 'vuex'

export default {
  data () {
    return {
      num: 2
    }
  },
  computed: {
    ...mapGetters(['isVuexCaptureHaveUndo', 'isVuexCaptureHaveRedo']),
    ...mapState({
      projectIndex: state => state.projectIndex
    })
  },
  components: {
  },
  mounted () {
  },
  methods: {
    undo () {
      this.$store.dispatch('vuex_action_undo')
    },
    redo () {
      this.$store.dispatch('vuex_action_redo')
    },
    sendLogger () {
      console.log(333333)
      this.$logworker.addLogger({ numtest: this.num++ })
    },
    addtest () {
      this.$store.commit('setProjectIndex__CAPTURE', this.num++)
    }
  }
}



